.page-company {
  .company-info-wrap {
    padding: map_get($global-spacing, a) 0 map_get($global-spacing, j);

    dl {
      max-width: map_get($global-width, content);
      display: flex;
      font-size: map_get($global-font-size, c);
      line-height: 1.56;

      dt {
        width: 300px;
      }

      dd {
        flex: 1;
      }

      &+dl {
        margin-top: map_get($global-spacing, h);
      }
    }
  }
}